ion) la publicidad es “la colocación de avisos y mensajes persuasivos, en tiempo o espacio, comprado en cualesquiera de los medios de comunicación por empresas lucrativas, organizaciones no lucrativas, agencias del estado y los individuos que intentan informar y/o persuadir a los miembros de un mercado meta en particular o a audiencias acerca de sus productos, servicios, organizaciones o ideas”. Citado por Puon (2013).")]),_c('br'),_c('p',[_vm._v("De acuerdo con ambas definiciones, la publicidad es una técnica de comunicación que se aplica en medios convencionales con objetivos comerciales; es decir, es pagada y busca el retorno de ese pago a través de la respuesta del mercado objetivo al que va dirigido el mensaje. La publicidad es programada en tiempo en los medios electrónicos como la televisión, la radio y actualmente en plataformas audiovisuales como YouTube y Facebook; así como en espacio en medios impresos como revistas y prensa o en sitios web a través de compra de pauta en Google AdWords.")])])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Relaciones públicas"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/tema1/img-1-3-2.png"),"alt":"Relaciones públicas"}})])]),_c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"h4"},[_vm._v("Relaciones públicas"),_c('br')]),_c('p',[_vm._v("Este tipo de comunicación es indispensable en cualquier empresa, no solo es apta para llegar a clientes potenciales y consumidores, sino también a proveedores y aliados estratégicos. Según Stanton, Etzel y Walker (2004), las relaciones públicas se definen como:")]),_c('br'),_c('p',[_vm._v("Una herramienta de administración destinada a influir favorablemente en las actitudes hacia la organización, sus productos y sus políticas. Es una forma de promoción que muchas veces se ignora, a diferencia de la mayor parte de la publicidad y de las ventas personales, no incluyen un mensaje de ventas específico. Los objetivos pueden ser clientes, accionistas, una organización gubernamental o un grupo de interés especial. (p. 643)")]),_c('br'),_c('p',[_vm._v("De acuerdo con esta definición, es una excelente técnica para mantener intacta la imagen de la empresa, porque permite a través de las relaciones con los diferentes públicos corregir posibles adversidades, generar confianza, crear lazos entre la marca y los públicos y mejorar la imagen corporativa.")])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Propaganda"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/tema1/img-1-3-3.png"),"alt":"Propaganda"}})])]),_c('div',{staticClass:"col-md-6"},[_c('div',{staticClass:"h4"},[_vm._v("Propaganda")]),_c('br'),_c('p',[_vm._v("Esta técnica suele confundirse con la publicidad; sin embargo, existe un abismo de diferencia entre ellas, si bien ambas pueden coincidir en el uso de algunos medios, su fin es muy diferente. La AMA (American Marketing Asociation) la define como “las ideas, información u otro material difundido comúnmente a través de los medios (periódicos, páginas amarillas, radio, televisión, etc.) en un esfuerzo por ganar a personas para una doctrina o punto de vista”. Citado por Thompson (2006). ")]),_c('br'),_c('p',[_vm._v("Con la llegada de las redes sociales se ha podido utilizar esta estrategia para propósitos comerciales y políticos más que nunca. Cuando se postea algo, ya sea de índole personal o comercial sin acudir a la publicidad paga en las redes sociales, se está haciendo propaganda; para el caso los likes que recibe dicho post o las veces que es compartido se convierte la propagación de una información sin haber pagado en los medios, solo por la convicción personal de quien los hizo, ya sea por gusto, por coincidencia ideológica o por apoyo a la causa. La propaganda es por lo tanto la divulgación sin ánimo de lucro de una idea, del concepto que