azo que fomentan la compra o venta de un producto o servicio” (p. 470-476). Dichos incentivos pueden llevarse a cabo de distintas formas: descuentos, ofertas, cupones, regalos, etc.")]),_c('br'),_c('p',[_vm._v("Esta técnica por lo tanto se realiza de forma casi exclusiva en los puntos de venta; no obstante, en la actualidad plataformas como Instagram y Facebook se han convertido en verdaderas vitrinas de los que antes fueron almacenes exclusivamente físicos, para transformarse en negocios multiplataforma, esto le ha permitido a muchas marcas expandir la cobertura de sus mercados y llegar a públicos que están por fuera de su área geográfica, todo acompañado de los cada vez mejores servicios de courier que tiene la posibilidad de llegar a casi cualquier lugar. Es por eso que hoy en día plataformas como Amazon y Ali Express permiten ventas a nivel mundial, a tal nivel que el propio comprador muchas veces ignora el origen del producto que está adquiriendo. La promoción de ventas es una muy eficaz técnica de comunicación debido a que más del 80 % de las compras que realiza una persona, las hace motivada por las emociones, por encima de la racionalidad; por lo tanto, las ofertas, los descuentos y los obsequios son un excelente generador de ventas por impulso. ")])])])])])],1),_vm._m(13),_vm._m(14),_vm._m(15),_c('br'),_c('br'),_c('br'),_c('col-md-12',[_c('div',{staticClass:"row justify-content-around align-items-center y1"},[_c('AcordionA',{staticClass:"col-md-10 mb-5",attrs:{"tipo":"b","clase-tarjeta":"tarjeta tarjeta--azul ta colc"}},[_c('div',{staticClass:"col-md-10 col-md-offset-10",attrs:{"titulo":"Televisión:"}},[_c('p',[_vm._v("Es un medio de información, audiovisual y conceptual. Sus características específicas son: ")]),_c('div',{staticClass:"row justify-content-around align-items-center"},[_c('div',{staticClass:"col-4"},[_c('figure',[_c('br'),_c('img',{staticStyle:{"width":"60%"},attrs:{"src":require("@/assets/tema1/img-1-4-3.png"),"alt":"Televisión"}})])]),_c('d